.header {
  position: relative;
  padding: 24px 0;
  background-color: var(--main-bg); }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center; }

@media (max-width: 600px) {
  .header {
    padding: 16px 0; } }

.header--component {
  background-color: var(--main-components-bg); }

.header__img {
  display: block;
  width: 110px;
  height: 32px; }

.header__icon {
  display: block;
  width: 110px;
  height: 32px; }

@media (max-width: 600px) {
  .header__icon {
    margin: 0 auto; } }

.header__toggler-mobile-menu {
  display: none;
  position: absolute;
  top: 25px;
  border: none;
  background-color: transparent;
  z-index: 5;
  width: 20px;
  height: 14px;
  cursor: pointer; }

@media (max-width: 600px) {
  .header__toggler-mobile-menu {
    display: block; } }

.header__toggler-mobile-menu .header__toggler-mobile-menu-icon,
.header__toggler-mobile-menu::before,
.header__toggler-mobile-menu::after {
  left: 0;
  position: absolute;
  height: 2px;
  width: 20px;
  transition: all 0.3s ease 0s;
  background-color: var(--menu-toggler-color); }

.header__toggler-mobile-menu::before,
.header__toggler-mobile-menu::after {
  content: ""; }

.header__toggler-mobile-menu::before {
  top: 0; }

.header__toggler-mobile-menu::after {
  bottom: 0; }

.header__toggler-mobile-menu .header__toggler-mobile-menu-icon {
  top: 50%;
  transform: scale(1) translate(0px, -50%); }

.header__toggler-mobile-menu--active .header__toggler-mobile-menu-icon {
  transform: scale(0) translate(0px, -50%);
  background-color: var(--active-menu-toggler-color); }

.header__toggler-mobile-menu--active::before {
  top: 50%;
  transform: rotate(-45deg) translate(0px, -50%);
  background-color: var(--active-menu-toggler-color); }

.header__toggler-mobile-menu--active::after {
  bottom: 50%;
  transform: rotate(45deg) translate(0px, 50%);
  background-color: var(--active-menu-toggler-color); }

.header__theme {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 124px; }

@media (max-width: 600px) {
  .header__theme {
    display: none; } }

.header__theme-icon--moon {
  width: 20px;
  height: 20px;
  color: var(--theme-togler-moon); }

.header__theme-icon--sun {
  width: 24px;
  height: 24px;
  color: var(--theme-togler-sun); }

.footer {
  padding-top: 71px;
  padding-bottom: 136px;
  background-color: var(--main-bg); }

@media (max-width: 400px) {
  .footer {
    display: none; } }

.footer__title {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 38px;
  line-height: 48px;
  color: var(--main-text-color);
  text-align: center;
  margin-bottom: 32px; }

.footer__text {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: var(--main-text-color);
  text-align: center;
  max-width: 640px;
  margin: 0 auto; }

.footer-components {
  padding: 56px 0;
  background-color: var(--main-components-bg); }

@media (max-width: 600px) {
  .footer-components {
    display: none; } }

.footer-components__title {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--grey-3);
  text-align: center; }

.toggler {
  cursor: pointer; }

.toggler__label {
  position: relative;
  display: flex;
  align-items: center;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: var(--radio-text-color);
  user-select: none; }

.toggler__input:disabled + .toggler__label {
  color: var(--grey-2); }

.toggler__input:focus + .toggler__label {
  text-decoration: underline; }

.toggler__label::before {
  content: "";
  display: block;
  margin-right: 12px;
  width: 52px;
  height: 28px;
  background-color: var(--white);
  border: 1px solid var(--grey-2);
  border-radius: 16px;
  transition-duration: 0.3s; }

.toggler--theme .toggler__label::before {
  margin-right: 0; }

.toggler__input:checked + .toggler__label::before {
  background: var(--blue);
  border-color: var(--blue); }

.toggler__input:disabled + .toggler__label::before {
  background-color: var(--grey-2);
  border-color: var(--grey-2); }

.toggler__label::after {
  content: "";
  position: absolute;
  left: 2px;
  width: 24px;
  height: 24px;
  background-color: var(--grey-2);
  border-radius: 50%;
  transition-duration: 0.4s; }

.toggler__input:checked + .toggler__label::after {
  background-color: var(--white);
  transform: translateX(24px); }

.toggler__input:disabled + .toggler__label::after {
  background-color: var(--grey-3);
  opacity: 0.25; }

.accordion {
  width: 100%; }

.accordion--preview {
  max-width: 600px;
  pointer-events: none; }

.accordion__item {
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: var(--main-text-color);
  display: block;
  padding-top: 24px;
  padding-right: 32px;
  padding-bottom: 24px;
  padding-left: 32px;
  margin-bottom: 8px;
  border: 1px solid var(--main-border-color);
  border-radius: 8px; }

@media (max-width: 400px) {
  .accordion--preview .accordion__item {
    font-size: 8px;
    line-height: 12px;
    padding-top: 12px;
    padding-right: 16px;
    padding-bottom: 12px;
    padding-left: 16px; } }

.accordion__item:last-child {
  margin-bottom: 0; }

.accordion__title {
  list-style: none;
  display: flex;
  align-items: center;
  position: relative; }

.accordion__title::-webkit-details-marker {
  display: none; }

.accordion__title::before {
  content: "";
  width: 18px;
  height: 10px;
  position: absolute;
  right: 0;
  background-image: url("../img/accordion-arrow-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition-duration: 0.3s;
  cursor: pointer; }

@media (max-width: 400px) {
  .accordion--preview .accordion__title::before {
    width: 9px;
    height: 5px;
    background-size: contain; } }

.accordion__item[open] .accordion__title::before {
  transform: rotate(-180deg); }

.button {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  white-space: nowrap;
  height: 48px;
  padding-top: 15px;
  padding-right: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  border: none;
  border-radius: 8px;
  transition-duration: 0.1s; }

.button:active {
  transform: translateY(4px); }

.button--preview {
  max-width: 250px;
  pointer-events: none; }
  @media (max-width: 400px) {
    .button--preview {
      font-size: 12px;
      line-height: 15px;
      height: auto;
      max-width: 120px;
      padding-top: 8px;
      padding-right: 8px;
      padding-bottom: 8px;
      padding-left: 8px; } }

.button--primary {
  color: var(--button-text-color);
  background-color: var(--primary); }

.button--primary:hover {
  background-color: var(--primary-hover); }

.button--secondary {
  color: var(--button-text-color);
  background-color: var(--secondary); }

.button--secondary:hover {
  background-color: var(--secondary-hover); }

.button--alternate {
  color: var(--main-text-color);
  background-color: transparent;
  border: 1px solid var(--rare-border-color); }

.button--alternate:hover {
  border-color: var(--main-border-color); }

.button--disabled,
.button:disabled {
  opacity: 0.5;
  pointer-events: none; }

.skeleton {
  width: 100%;
  padding: 24px 40px 32px 24px;
  border: 1px solid var(--rare-border-color);
  border-radius: 8px; }

.skeleton--component {
  max-width: 600px; }

.skeleton--preview {
  max-width: 600px;
  pointer-events: none; }

@media (max-width: 400px) {
  .skeleton--preview {
    padding: 12px 20px 16px 12px; } }

.skeleton__line {
  height: 20px; }

@media (max-width: 400px) {
  .skeleton--preview .skeleton__line {
    height: 10px; } }

.skeleton__inner {
  width: 66%;
  display: flex;
  align-items: center;
  margin-bottom: 24px; }

@media (max-width: 400px) {
  .skeleton--preview .skeleton__inner {
    margin-bottom: 12px; } }

.skeleton__avatar {
  flex-shrink: 0;
  width: 64px;
  height: 64px;
  margin-right: 16px;
  border-radius: 50%;
  background-color: var(--grey-2);
  background-image: var(--grey-gradient); }

.skeleton__line,
.skeleton__avatar {
  background-color: var(--grey-2);
  background-image: var(--grey-gradient);
  background-size: 200%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: gradient;
  animation-timing-function: linear; }

@keyframes gradient {
  to {
    background-position: 200%; } }

@media (max-width: 400px) {
  .skeleton--preview .skeleton__avatar {
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    margin-right: 8px;
    background-color: var(--grey-2);
    border-radius: 50%; } }

.skeleton__info {
  width: 77%; }

.skeleton__line--name {
  width: 67%;
  margin-bottom: 8px; }

.skeleton__line--subname {
  width: 100%; }

.skeleton__line--text {
  width: 100%;
  margin-bottom: 8px; }

.skeleton__line--text:last-child {
  margin-bottom: 0; }

.tooltip {
  display: inline-block;
  position: relative; }

.tooltip--previev {
  pointer-events: none; }

.tooltip__icon {
  width: 24px;
  height: 24px;
  color: var(--main-text-color); }

.tooltip__bubble {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: var(--grey-2);
  white-space: nowrap;
  display: none;
  position: absolute;
  bottom: calc(100% + 8px);
  left: 50%;
  transform: translateX(-50%);
  padding: 13px 21px 8px;
  background-color: var(--blue);
  box-shadow: 0px 2px 4px var(--box-shadow);
  border-radius: 10px; }

.tooltip--previev .tooltip__bubble {
  display: block; }

.tooltip__bubble::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 8px solid transparent;
  border-top-color: var(--blue); }

.tooltip__icon:hover ~ .tooltip__bubble {
  display: block; }

.histogram {
  width: 100%;
  padding: 16px 24px 24px;
  border: 1px solid var(--rare-border-color);
