  position: relative;
  float: left;
  max-height: 1px;
  overflow: hidden;
  z-index: -1;
  padding: 0;
  margin: 0;
  pointer-events: none;
  flex-grow: inherit;
  flex-shrink: 0;
  flex-basis: 0; }

.simplebar-height-auto-observer {
  box-sizing: inherit;
  display: block;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 1000%;
  width: 1000%;
  min-height: 1px;
  min-width: 1px;
  overflow: hidden;
  pointer-events: none;
  z-index: -1; }

.simplebar-track {
  z-index: 1;
  position: absolute;
  right: 0;
  bottom: 0;
  pointer-events: none;
  overflow: hidden; }

[data-simplebar].simplebar-dragging .simplebar-content {
  pointer-events: none;
  user-select: none;
  -webkit-user-select: none; }

[data-simplebar].simplebar-dragging .simplebar-track {
  pointer-events: all; }

.simplebar-scrollbar {
  position: absolute;
  left: 0;
  right: 0;
  min-height: 10px; }

.simplebar-scrollbar:before {
  position: absolute;
  content: '';
  background: black;
  border-radius: 7px;
  left: 2px;
  right: 2px;
  z-index: 100; }

.simplebar-scrollbar.simplebar-visible:before {
  /* When hovered, remove all transitions from drag handle */
  opacity: 1; }

.simplebar-track.simplebar-vertical {
  top: 0;
  width: 11px; }

.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
  top: 2px;
  bottom: 2px; }

.simplebar-track.simplebar-horizontal {
  left: 0;
  height: 11px; }

.simplebar-track.simplebar-horizontal .simplebar-scrollbar:before {
  height: 100%;
  left: 2px;
  right: 2px; }

.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
  right: auto;
  left: 0;
  top: 2px;
  height: 7px;
  min-height: 0;
  min-width: 10px;
  width: auto; }

/* Rtl support */
[data-simplebar-direction='rtl'] .simplebar-track.simplebar-vertical {
  right: auto;
  left: 0; }

.hs-dummy-scrollbar-size {
  direction: rtl;
  position: fixed;
  opacity: 0;
  visibility: hidden;
  height: 500px;
  width: 500px;
  overflow-y: hidden;
  overflow-x: scroll; }

.simplebar-hide-scrollbar {
  position: fixed;
  left: 0;
  visibility: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none; }

/* Pretty printing styles. Used with prettify.js. */
/* Vim sunburst theme by David Leibovic */
pre .str, code .str {
  color: #65B042; }

/* string  - green */
pre .kwd, code .kwd {
  color: #E28964; }

/* keyword - dark pink */
pre .com, code .com {
  color: #AEAEAE;
  font-style: italic; }

/* comment - gray */
pre .typ, code .typ {
  color: #89bdff; }

/* type - light blue */
pre .lit, code .lit {
  color: #3387CC; }

/* literal - blue */
pre .pun, code .pun {
  color: #bdb76b; }

/* punctuation - white */
pre .pln, code .pln {
  color: #989788; }

/* plaintext - white */
pre .tag, code .tag {
  color: #89bdff; }

/* html/xml tag    - light blue */
pre .atn, code .atn {
  color: #bdb76b; }

/* html/xml attribute name  - khaki */
pre .atv, code .atv {
  color: #65B042; }

/* html/xml attribute value - green */
pre .dec, code .dec {
  color: #3387CC; }

/* decimal - blue */
pre.prettyprint, code.prettyprint {
  border-radius: 8px; }

pre.prettyprint {
  width: 95%;
  margin: 1em auto;
  padding: 1em;
  white-space: pre-wrap; }

/* Specify class=linenums on a pre to get line numbering */
ol.linenums {
  margin-top: 0;
  margin-bottom: 0;
  color: #AEAEAE; }

/* IE indents via margin-left */
li.L0, li.L1, li.L2, li.L3, li.L5, li.L6, li.L7, li.L8 {
  list-style-type: none; }

/* Alternate shading for lines */
@media print {
  pre .str, code .str {
    color: #060; }
  pre .kwd, code .kwd {
    color: #006;
    font-weight: bold; }
  pre .com, code .com {
    color: #600;
    font-style: italic; }
  pre .typ, code .typ {
    color: #404;
    font-weight: bold; }
  pre .lit, code .lit {
    color: #044; }
  pre .pun, code .pun {
    color: #440; }
  pre .pln, code .pln {
    color: #000; }
  pre .tag, code .tag {
    color: #006;
    font-weight: bold; }
  pre .atn, code .atn {
    color: #404; }
  pre .atv, code .atv {
    color: #060; } }
